import { notification } from 'antd';

export const notificationWrapper = (error, message) => {
  if (error) {
    return notification.error({
      message: '–ü–æ–º–∏–ª–∫–∞',
      description: message,
    });
  }
  return notification.success({
    message: '–£—Å–ø—ñ—Ö',
    description: message,
  });
};
